/* DOCS HERE: https://github.com/ua-oira/react-super-responsive-table */
/* inspired by: https://css-tricks.com/responsive-data-tables/ */
.responsiveTable {
  width: 100%; }

.responsiveTable td .tdBefore {
  display: none; }

.responsiveTable td {
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  width: 20%;
  text-overflow: ellipsis; }
  .responsiveTable td .neutralize {
    display: inline-block; }
    .responsiveTable td .neutralize.node {
      width: auto;
      max-width: calc(64% - 30px); }
  .responsiveTable td span {
    display: inline-block;
    text-overflow: ellipsis;
    overflow: hidden;
    margin: auto 0;
    transform: translateY(7px); }
    .responsiveTable td span.node {
      max-width: 694px; }
    .responsiveTable td span.name {
      max-width: 90%; }
    .responsiveTable td span.space-around {
      margin: auto 10px;
      color: #68686E; }

@media screen and (min-width: 1231px) and (max-width: 1700px) {
  .responsiveTable td span.node {
    max-width: 379px !important; } }

@media screen and (max-width: 1230px) {
  .responsiveTable td span.node {
    max-width: 160px !important; } }

@media screen and (max-width: 1010px) {
  .responsiveTable td span.name {
    max-width: 160px !important; } }

@media screen and (max-width: 870px) {
  .responsiveTable td span.name {
    max-width: 140px !important; } }

@media screen and (max-width: 762px) {
  /*
      Force table elements to not behave like tables anymore
      Hide table headers (but not display: none;, for accessibility)
    */
  .responsiveTable table,
  .responsiveTable thead,
  .responsiveTable tbody,
  .responsiveTable th,
  .responsiveTable td,
  .responsiveTable tr {
    display: block; }
  .responsiveTable thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
    border-bottom: 2px solid #333; }
  .responsiveTable tbody tr {
    border-bottom: 1px solid #26252a; }
  .responsiveTable td.pivoted {
    /* Behave like a "row" */
    border: none !important;
    position: relative;
    width: calc(100% - 128px);
    padding-left: 118px !important;
    padding-right: 10px;
    text-align: left !important;
    white-space: pre-wrap;
    overflow-wrap: break-word; }
    .responsiveTable td.pivoted span {
      transform: unset;
      display: inline-block;
      text-overflow: ellipsis;
      overflow: hidden;
      margin: auto 0; }
      .responsiveTable td.pivoted span.name {
        max-width: 300px; }
      .responsiveTable td.pivoted span.node {
        max-width: 150px; }
      .responsiveTable td.pivoted span.space-around {
        margin: 0 10px; }
  .responsiveTable td .tdBefore {
    /* Now like a table header */
    position: absolute;
    display: block;
    font-weight: normal;
    color: #68686E;
    font-size: 14px;
    /* Top/left values mimic padding */
    left: 19px;
    width: calc(50% - 20px);
    white-space: pre-wrap;
    overflow-wrap: break-word;
    text-align: left !important; } }

.event-list {
  position: relative;
  min-height: 597px; }
  .event-list table {
    border-collapse: collapse; }
    .event-list table thead tr th {
      padding: 10px 15px;
      text-align: left;
      color: #68686E;
      font-size: 14px;
      font-weight: normal;
      cursor: pointer; }
      .event-list table thead tr th svg {
        display: none;
        margin-left: 5px;
        transform: translateY(2px); }
      .event-list table thead tr th.selected svg {
        display: inline-block !important; }
    .event-list table tbody tr {
      border-bottom: 1px solid #26252a; }
      .event-list table tbody tr:hover {
        background-color: rgba(0, 0, 0, 0.1);
        cursor: pointer; }
      @media screen and (max-width: 700px) {
        .event-list table tbody tr:hover {
          background-color: unset; }
        .event-list table tbody tr.dark {
          background-color: rgba(0, 0, 0, 0.1); } }
      .event-list table tbody tr.last {
        border-bottom: 0; }
      .event-list table tbody tr td {
        padding: 0 15px;
        color: white;
        font-size: 14px;
        font-weight: normal;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        line-height: 37px; }
        .event-list table tbody tr td .muted {
          color: #68686E; }
  @media screen and (max-width: 762px) {
    .event-list svg {
      display: none !important; } }
